import { collection, getDoc, doc, getDocs, setDoc } from "firebase/firestore";
import React, { useEffect, useState } from "react";
import { Container, Form, Navbar, Button, ButtonGroup } from "react-bootstrap";
import db from "./firebase";
import useFirestore from "./useFirestore";

const Links = () => {
	const { data, loading, saveData } = useFirestore("links", "links");

	const [editIndex, setEditIndex] = useState(null);
	const [editedTitle, setEditedTitle] = useState("");
	const [editedURL, setEditedURL] = useState("");

	const handleEdit = (index) => {
		setEditIndex(index);
		setEditedTitle(data.links[index].title);
		setEditedURL(data.links[index].url);
	};

	const handleSave = async (index) => {
		const updatedLinks = [...data.links];
		updatedLinks[index] = { title: editedTitle, url: editedURL };
		await saveData({ links: updatedLinks });
		setEditIndex(null);
	};

	const handleCancel = () => {
		setEditIndex(null);
	};

	const handleDelete = async (index) => {
		const updatedLinks = data.links.filter((_, i) => i !== index);
		await saveData({ links: updatedLinks });
	};

	const handleAddNew = async () => {
		const updatedLinks = [...data.links, { title: "New Link", url: "url" }];

		await saveData({ links: updatedLinks });
	};

	const moveLink = async (index, direction) => {
		const updatedLinks = [...data.links];

		if (direction === "up" && index > 0) {
			[updatedLinks[index], updatedLinks[index - 1]] = [
				updatedLinks[index - 1],
				updatedLinks[index],
			];
		} else if (direction === "down" && index < updatedLinks.length - 1) {
			// Swap with the next link
			[updatedLinks[index], updatedLinks[index + 1]] = [
				updatedLinks[index + 1],
				updatedLinks[index],
			];
		}

		await saveData({ links: updatedLinks });
	};

	return (
		<div>
			<Navbar expand="lg" className="bg-body-tertiary">
				<Container>
					<Navbar.Brand className="mx-auto">Kumar EA's</Navbar.Brand>
				</Container>
			</Navbar>

			<Container className="mt-4">
				{loading ? (
					<h3>Loading...</h3>
				) : (
					data.links.map((link, index) => (
						<div key={index}>
							{editIndex === index ? (
								<div>
									<Form.Control
										type="text"
										value={editedTitle}
										onChange={(e) =>
											setEditedTitle(e.target.value)
										}
										className="mb-2"
										placeholder="Edit title"
									/>

									<Form.Control
										type="text"
										value={editedURL}
										onChange={(e) =>
											setEditedURL(e.target.value)
										}
										className="mb-2"
										placeholder="Edit URL"
									/>

									<div className="mt-2">
										<Button
											variant="success"
											onClick={() => handleSave(index)}
											className="me-2"
										>
											Save
										</Button>

										<Button
											variant="secondary"
											onClick={handleCancel}
										>
											Cancel
										</Button>
									</div>
								</div>
							) : (
								<div className="d-flex justify-content-between align-items-center">
									<h3>
										<a
											href={link.url}
											target="_blank"
											rel="noopener noreferrer"
										>
											{link.title}
										</a>
									</h3>
									<div>
										<Button
											variant="warning"
											onClick={() => handleEdit(index)}
										>
											Edit
										</Button>

										<Button
											variant="info"
											onClick={() =>
												moveLink(index, "up")
											}
											disabled={index === 0}
										>
											
										</Button>
										<Button
											variant="info"
											onClick={() =>
												moveLink(index, "down")
											}
											disabled={
												index === data.links.length - 1
											}
										>
											
										</Button>

										<Button
											variant="danger"
											onClick={() => handleDelete(index)}
										>
											Delete
										</Button>
									</div>
								</div>
							)}

							{/* <h3>
								<a href={link.url} target="_blank">
									{link.title}
								</a>
							</h3> */}
						</div>
					))
				)}

				<Button
					variant="primary"
					onClick={handleAddNew}
					className="mt-2"
				>
					Add New Link
				</Button>

				<hr />
			</Container>
		</div>
	);
};

export default Links;
