import React, { useState, useEffect } from "react";
import { Container, Form, Button } from "react-bootstrap";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import useFirestore from "./useFirestore"; // Import your custom hook
import "./clipboard.css";

const Clipboard = () => {
	const [text, setText] = useState("");
	const [status, setStatus] = useState("Loading..."); // Set initial status as "Loading..."
	const [hasChanges, setHasChanges] = useState(false); // External changes
	const [isDirty, setIsDirty] = useState(false); // Local unsaved changes
	const [lastSavedText, setLastSavedText] = useState(""); // Track last saved text

	// Use the custom hook to manage Firestore data
	const { data, loading, saveData, fetchData } = useFirestore(
		"ea-main",
		"ea-main"
	);

	useEffect(() => {
		if (data) {
			setText(data.text || "");
			setLastSavedText(data.text || "");
			setIsDirty(false);
			const currentTime = new Date().toLocaleString(); // Get current date and time
			setStatus(`Loaded - ${currentTime}`); // Update status once the data is loaded
		} else if (!loading) {
			setStatus("No data found"); // In case no data is found after loading
		}
	}, [data, loading]);

	// Check for external changes from Firestore
	const checkForChanges = async () => {
		if (!isDirty) {
			fetchData(); // Refresh data
			if (data && data.text !== lastSavedText) {
				setHasChanges(true);
			}
		}
	};

	useEffect(() => {
		const interval = setInterval(() => {
			checkForChanges();
		}, 1 * 60 * 1000); // Check every minute

		return () => clearInterval(interval);
	}, [lastSavedText, isDirty]);

	// Handle user input (local changes)
	const handleChange = (e) => {
		if (!loading && !hasChanges) {
			setText(e.target.data);
			setIsDirty(true);
		}
	};

	// Handle Save button click
	const handleSave = async () => {
		await saveData({ text }); // Use the custom hook's save function
		alert("Clipboard saved successfully");

		setLastSavedText(text); // Update last saved state after saving
		setIsDirty(false);
		setHasChanges(false);
		setStatus(`Saved - ${new Date().toLocaleString()}`); // Update status after saving
	};

	// Handle Copy button click
	const handleCopy = () => {
		navigator.clipboard
			.writeText(text)
			.then(() => alert("Text copied to clipboard"))
			.catch((err) => console.error("Error copying text: ", err));
	};

	// Handle Refresh button click (to load server-side changes)
	const handleRefresh = () => {
		fetchData();
		setHasChanges(false);
	};

	return (
		<div>
			<Form>
				<Container>
					<Form.Group
						className="mb-3 mt-4"
						controlId="clipboard-textarea"
					>
						<Form.Label className="text-xl">
							<h5>EA Notes</h5>
						</Form.Label>

						<div>
							<Form.Label>
								<h6>{status}</h6>{" "}
								{/* Status is displayed here */}
							</Form.Label>
						</div>

						{isDirty && (
							<div>
								<Form.Label>
									<h6>{isDirty && "You've made changes"}</h6>
								</Form.Label>
							</div>
						)}

						{hasChanges && (
							<div className="alert alert-danger" role="alert">
								<h3 className="">New changes!</h3>
								<Button
									variant="danger"
									onClick={handleRefresh}
									size="lg"
								>
									Refresh
								</Button>
							</div>
						)}

						<div>
							<Button
								variant="primary"
								onClick={handleSave}
								className="me-4"
								size="lg"
								disabled={hasChanges || !isDirty} // Disable if no local changes or external changes exist
							>
								Save
							</Button>

							<Button
								variant="secondary"
								onClick={handleCopy}
								size="lg"
							>
								Copy
							</Button>
						</div>

						<Form.Control
							as="textarea"
							rows={30}
							value={text}
							onChange={handleChange}
							disabled={loading || hasChanges} // Disable only if it's loading or external changes are detected
							className="mt-4"
						/>

						{/* <ReactQuill
							value={text}
							readOnly={loading || hasChanges}
							onChange={handleChange}
							theme="snow"
						/> */}
					</Form.Group>
				</Container>
			</Form>
		</div>
	);
};

export default Clipboard;
